<template>
  <!--  style="position: fixed;top: 0;z-index: 2;width: 100vh;"  -->
  <div   style="position: fixed;top: 0;z-index: 2;width: 100vw;" >
    <van-tabs :active="activeTabs" sticky  @change="changeActiveTabs">
    <van-tab
      v-for="(item) in tabs"
      :key="item.key"
      :title="item.title"
      :name="item.key"
    >
    </van-tab>
  </van-tabs>
  <van-tabs :key="activeTabs" sticky v-if="!!tabs.find(tab => tab.key === activeTabs).chidlren.length" swipeable :active="activeChildrenTabs" @change="changeActiveChildrenTabs">
        <van-tab
          v-for="(chidlren) in tabs.find(tab => tab.key === activeTabs).chidlren"
          :key="chidlren.key"
          :title="chidlren.title"
          :name="chidlren.key"
        >
        </van-tab>
      </van-tabs>
  </div>

  <div class="image-contain">
          <van-image
            class="image-item"
            v-for="(photo, index) in requiredList"
            :key="photo"
            width="150"
            fit="cover"
            :src="photo"
            @click="previewImg(index)"
            />
        </div>

</template>

<script  setup >
import { showImagePreview } from 'vant'
import { nextTick, watch, ref } from 'vue'

const tabs = [
  {
    key: 'cartoon',
    title: '漫展场照',
    count: 32,
    chidlren: [
      {
        key: 'ijoy',
        title: 'ijoy'
      },
      {
        key: 'ido',
        title: 'ido'
      },
      {
        key: 'ug',
        title: '万游引力'
      },
      {
        key: 'ilke',
        title: 'ilke'
      }
    ]
  },
  {
    key: 'portrait',
    title: '人像',
    count: 32,
    chidlren: [
      {
        key: '798',
        title: '798'
      },
      {
        key: 'park',
        title: '地坛公园'
      },
      {
        key: 'yby',
        title: '园博园'
      },
      {
        key: 'yijia',
        title: '宜家'
      }
    ]
  },
  {
    key: 'citywalk',
    title: '扫街',
    count: 32,
    chidlren: [
      {
        key: 'shanghai',
        title: '上海'
      },
      {
        key: 'wangfujing',
        title: '王府井'
      }
    ]
  },
  {
    key: 'animal',
    title: '动物',
    count: 32,
    chidlren: []
  },
  {
    key: 'static',
    title: '静物',
    count: 32,
    chidlren: []
  }
]

const countMap = new Map([
  ['ijoy', 32],
  ['ido', 54],
  ['ilke', 20],
  ['ug', 9],
  ['798', 18],
  ['park', 10],
  ['yby', 13],
  ['shanghai', 8],
  ['wangfujing', 18]
])

const loading = ref(false)

const activeTabs = ref('cartoon')

const activeChildrenTabs = ref('ijoy')

const changeActiveTabs = (value) => {
  activeTabs.value = value
}

const changeActiveChildrenTabs = (value) => {
  console.log('出发11')
  activeChildrenTabs.value = value
}

watch(
  () => activeTabs.value,
  () => {
    loading.value = true
    // activeChildrenTabs.value = tabs.find(tab => tab.key === activeTabs.value).chidlren[0].key
    console.log('value:', activeChildrenTabs.value)
    loading.value = false
  }
)

const requiredList = ref([])

watch(
  () => activeChildrenTabs.value,
  () => {
    console.log('出发:', new Array(countMap.get(activeChildrenTabs.value)))
    nextTick(() => {
      requiredList.value = new Array(countMap.get(activeChildrenTabs.value)).fill(1).map((item, index) => require('@/assets/' + activeTabs.value + '/' + activeChildrenTabs.value + '/' + (index + 1) + '.jpeg'))
    })
  },
  {
    immediate: true
  }
)

const previewImg = (index) => {
  console.log('new Array(countMap.get(activeChildrenTabs)).fill(1):', activeChildrenTabs.value)
  showImagePreview({
    images: requiredList.value,
    startPosition: index
  })
}
</script>

